.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header__link {
  width: 40px;
  height: 40px;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.header__logo {
  width: 40px;
  height: 40px;
}

.nav {
  margin: 0;
  padding: 0;
  display: flex;
}

.nav__list {
  width: 100%;
  height: 60px;
  display: flex;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  align-content: center;
  block-size: 20px;
  padding-top: 23px;
  padding-bottom: 22px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 2px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: -15px;
}

* {
  box-sizing: border-box;
  margin: 0;
  font-family: Roboto, sans-serif;
}

.container {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  row-gap: 46px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .container {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .container {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .container {
    grid-template-columns: repeat(4, 200px);
  }
}

.stars {
  display: flex;
}

.stars__star {
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.stars--4 .stars__star:nth-child(5) {
  background-image: url("star.822d561e.svg");
}

.review {
  text-align: right;
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  margin: auto;
  padding: 32px 16px 16px;
  transition: transform .3s;
  position: relative;
}

.card:hover {
  transform: scale(1.2);
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: flex;
}

.card__name {
  text-align: left;
  color: #060b35;
  justify-content: center;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__productCode {
  text-align: left;
  color: #616070;
  padding-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__review {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.card__price .card__text1 {
  text-align: left;
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price .card__text2 {
  text-align: right;
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__buy {
  color: #fff;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card__buy:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

body {
  margin: 0;
}
